#components-layout-demo-custom-trigger {
  height: 100%;
  width: 100%;
}
#components-layout-demo-custom-trigger .ant-btn-link {
  color: #8f8f8f;
}
#components-layout-demo-custom-trigger .ant-btn-link:hover {
  color: #40a9ff;
}
#components-layout-demo-custom-trigger .ant-layout-header,
#components-layout-demo-custom-trigger .ant-layout-sider {
  background-color: #fff;
}
#components-layout-demo-custom-trigger .left {
  border-right: 1px solid #efefef;
}
#components-layout-demo-custom-trigger .left .ask-box {
  height: 260px;
  padding: 20px 20px;
}
#components-layout-demo-custom-trigger .left .history {
  height: calc(100% - 325px);
  padding: 0 10px 0 10px;
  overflow-y: auto;
}
#components-layout-demo-custom-trigger .left .history .title {
  padding: 0 10px 0 10px;
  height: 50px;
  line-height: 50px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
}
#components-layout-demo-custom-trigger .left .history .title .anticon {
  float: right;
  margin-top: 14px;
}
#components-layout-demo-custom-trigger .left .history .item {
  padding: 0 10px 0 10px;
  height: 35px;
  line-height: 35px;
  border-bottom: 1px solid #efefef;
  cursor: pointer;
}
#components-layout-demo-custom-trigger .left .history .item:last-child {
  border-bottom: 0;
}
#components-layout-demo-custom-trigger .left .history .item:hover {
  background-color: #f9f9f9;
  border-radius: 5px;
}
#components-layout-demo-custom-trigger .left .history .item span {
  display: inline-block;
  width: 100%;
  white-space: nowrap;
  /* 防止文本换行 */
  overflow: hidden;
  /* 隐藏溢出的文本 */
  text-overflow: ellipsis;
  /* 显示省略号 */
}
#components-layout-demo-custom-trigger .left .history .active {
  background-color: #efefef;
  border-radius: 5px;
}
#components-layout-demo-custom-trigger .left .history .active span {
  width: 80%;
}
#components-layout-demo-custom-trigger .left .footer {
  height: 64px;
  border-top: 1px solid #efefef;
  line-height: 64px;
  padding-left: 20px;
}
#components-layout-demo-custom-trigger .left .footer .trigger {
  right: 0;
}
#components-layout-demo-custom-trigger .left .footer .user-mannual {
  cursor: pointer;
}
#components-layout-demo-custom-trigger .left .footer .user-mannual:hover {
  color: #40a9ff;
}
#components-layout-demo-custom-trigger .right {
  position: relative;
}
#components-layout-demo-custom-trigger .right .content .title {
  height: 50px;
  color: #000;
  line-height: 50px;
  font-weight: bold;
  font-size: 20px;
}
#components-layout-demo-custom-trigger .right .content .tabs span {
  display: inline-block;
  width: auto;
  padding: 10px;
  background-color: #eff0f2;
  font-weight: bold;
  border-radius: 5px;
  color: #000;
  cursor: pointer;
}
#components-layout-demo-custom-trigger .right .content .tabs span:hover {
  opacity: 0.8;
}
#components-layout-demo-custom-trigger .right .content .tabs .active {
  background-color: #d6e3fc;
  color: #165eed;
}
#components-layout-demo-custom-trigger .right .content .message {
  padding: 10px;
  border-radius: 5px;
  background-color: #f5f6f8;
  margin-top: 10px;
}
#components-layout-demo-custom-trigger .right .content .message .title {
  height: 40px;
  line-height: 40px;
  font-size: 16px;
  font-weight: bold;
}
#components-layout-demo-custom-trigger .right .content .message .logic-content {
  min-height: 30px;
}
#components-layout-demo-custom-trigger .right .content .message .blink {
  animation: blink 1s infinite;
  display: inline-block;
  height: 15px;
  width: 2px;
  transition: all 0.3s;
  background-color: #8f8f8f;
}
#components-layout-demo-custom-trigger .right .content .recommend-container {
  margin-top: 10px;
  border-radius: 5px;
  background-color: #f5f6f8;
  padding-bottom: 10px;
}
#components-layout-demo-custom-trigger .right .content .recommend-container .title {
  height: 40px;
  line-height: 40px;
  font-size: 16px;
  font-weight: bold;
  padding-left: 10px;
}
#components-layout-demo-custom-trigger .right .trigger {
  bottom: 0;
  left: -20px;
  position: fixed;
  z-index: 1;
}
#components-layout-demo-custom-trigger .trigger {
  font-size: 18px;
  line-height: 64px;
  padding: 0 24px;
  cursor: pointer;
  transition: color 0.3s;
  position: absolute;
}
#components-layout-demo-custom-trigger .trigger:hover {
  color: #1890ff;
}
#components-layout-demo-custom-trigger .logo {
  height: 32px;
  background: rgba(255, 255, 255, 0.2);
  margin: 16px;
}
.slide-enter-active {
  animation: slide-in 0.5s;
}
.slide-leave-active {
  animation: slide-out 0.5s;
}
@keyframes blink {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes slide-in {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0);
  }
}
@keyframes slide-out {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-100%);
  }
}
